function Table(props) {
  // handles changes to the score of each of song dynamically
  const changeScore = useCallback(
    async event => {
      await fetch("/api/add", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        // the body of this song is built from state
        body: JSON.stringify({
          song: name,
          score: score
        })
      });

      // forces a call to the hook useSWR
      await mutate();

      // update sent
      setSent(true);
    },
    [name, score]
  );

  return (
    <tr>
      <td>{props.song}</td>
      <td>{props.score}</td>
      <td>
        <button
          onClick={() => {
            props;
          }}
        >
          upvote
        </button>
      </td>
      <td>
        <button>downvote</button>
      </td>
    </tr>
  );
}

export default Table;
